@import url("https://fonts.googleapis.com/css?family=Josefin+Sans:300,400,400i|Nunito:300,300i");
:root {
  --clr-primary: #c69963;
  --clr-primary-dark: #b28451;
  --clr-secondary: rgb(16, 29, 44);
  --clr-grey-light-1: #f9f7f6;
  --clr-grey-light-2: #aaa;
  --clr-grey-dark-1: #54483a;
  --clr-grey-dark-2: #6d5d4b;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

ul {
  list-style-type: none;
}

a:link,
a:visited {
  text-decoration: none;
}

:root {
  --font-primary: "Nunito", sans-serif;
  --font-display: "Josefin Sans", sans-serif;
}

.heading-4, .heading-3, .heading-2, .heading-1 {
  font-family: var(--font-display);
}

body {
  font-family: var(--font-primary);
  color: var(clr-grey-dark-2);
  font-weight: 300;
  line-height: 1.6;
}

.heading-1 {
  font-size: 4.5rem;
  color: var(--clr-grey-light-1);
  line-height: 1;
}

.heading-2 {
  font-size: 4rem;
  font-style: italic;
  line-height: 1;
}
.heading-2--light {
  color: var(--clr-grey-light-1);
}
.heading-2--dark {
  color: var(--clr-grey-dark-1);
}

.heading-3 {
  font-size: 1.6rem;
  color: var(--clr-primary);
  text-transform: uppercase;
}

.heading-4 {
  font-size: 1.9rem;
}
.heading-4--light {
  color: var(--clr-grey-light-1);
}
.heading-4--dark {
  color: var(--clr-grey-dark-1);
}

.features {
  grid-column: center-start/center-end;
  margin: 15rem 0;
}

.footer {
  background-color: var(--clr-secondary);
  grid-column: full-start/full-end;
}

.gallery {
  background-color: blueviolet;
  grid-column: full-start/full-end;
}

.header {
  background-color: var(--clr-grey-light-1);
  grid-column: full-start/col-end 6;
}

.homes {
  background-color: aquamarine;
  grid-column: full-start/full-end;
}

.realtors {
  background-color: var(--clr-secondary);
  grid-column: col-start 7/full-end;
}

.sidebar {
  background-color: var(--clr-primary);
  grid-row: 1/-1;
}

.story__pictures {
  background-color: lawngreen;
  grid-column: full-start/col-end 4;
}

.story__content {
  background-color: var(--clr-primary-dark);
  grid-column: col-start 5/full-end;
}

.container {
  display: grid;
  grid-template-rows: 80vh min-content 40vw repeat(3, min-content);
  grid-template-columns: [sidebar-start] 8rem [sidebar-end full-start] 1fr [center-start] repeat(8, [col-start] minmax(min-content, 15rem) [col-end]) [center-end] 1fr [full-end];
}

.mb-sm {
  margin-bottom: 2rem;
}

.mb-md {
  margin-bottom: 3rem;
}

.mb-lg {
  margin-bottom: 4rem;
}

.mb-hg {
  margin-bottom: 8rem;
}